<template>
  <div class="app-container public-table">

    <el-input placeholder="请输入搜索内容..." v-model="inputText">
      <el-select v-model="selectType" slot="prepend" style="width: 120px">
        <el-option label="UID" value="encode_uids"></el-option>
        <el-option label="IP" value="ips"></el-option>
        <el-option label="类型" value="user_type"></el-option>
        <el-option label="手机" value="mobiles"></el-option>
        <el-option label="邮箱" value="emails"></el-option>
        <el-option label="时间" value="dates"></el-option>
      </el-select>
      <i v-if="isSelectDate" slot="prefix" class="el-input__icon el-icon-date" @click="searchDate"></i>
      <el-tag v-for="(item, index) in tagList" :key="index" slot="suffix" size="small" closable @close="removeTags(index)">{{item}}</el-tag>
      <i slot="suffix" class="el-input__icon el-icon-success" @click="addTags"></i>
      <el-button slot="append" icon="el-icon-search" @click="submitSearch"></el-button>
    </el-input>
    <el-date-picker class="reset-date-picker" ref="datePicker" @change="datePickerChange" unlink-panels value-format="yyyy-MM-dd"
      v-model="selectDate" slot="prepend" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
    </el-date-picker> 

    <el-table class="table-content" :data="tableList" border show-summary v-loading="loading" height="calc(100% - 120px)" @selection-change="handleSelectionChange" :summary-method="getSummaries">
      <el-table-column type="selection" width="60" align="center"></el-table-column>
      <el-table-column prop="tableUid" label="UID" width="100" align="center"></el-table-column>
      <el-table-column prop="tableUserId" label="UID" width="100" align="center"></el-table-column>
      <el-table-column prop="tableUserType" label="用户类型" align="center"></el-table-column>
      <el-table-column prop="tablePhone" label="用户手机" width="120" align="center"></el-table-column>
      <el-table-column prop="tableEmail" label="用户邮箱" width="200" align="center"></el-table-column>
      <el-table-column prop="tableCreateTime" label="注册时间" width="200" align="center"></el-table-column>
      <el-table-column prop="tableCreateIP" label="注册IP" width="300" align="center"></el-table-column>
      <el-table-column prop="tableGgAuth" label="谷歌验证" align="center"></el-table-column>
      <!-- <el-table-column prop="tableActivated" label="邮箱激活" align="center"></el-table-column> -->

    </el-table>

    <el-pagination background @size-change="handleSizeChange" @current-change="handlePageChange" :current-page="currentPage" :page-sizes="[20, 30, 40, 50]" :page-size="currentSize" :total="currentTotal" layout="total, sizes, prev, pager, next, jumper">
    </el-pagination>

  </div>
</template>

<script>
import TableMixin from '@/mixins/Table'
import { UidEncode } from '@/utils/common'
export default {
  mixins: [TableMixin],
  data() {
    return {
      action: 0,
      searchName: 'searchUser',
      moduleName: 'fetchUserList',
      tableList: [],
      multipleSelection: [],
      filterSummaries: []  // 需要统计的字段
    }
  },
  methods: {
    setListData(data) {
      const tempArray = []
      data.forEach(elm => {
        tempArray.push({
          tableUid: UidEncode(elm.Uid),
          tableUserId: elm.Uid,
          tableUserType: elm.Type === 1 ? '普通' : '内部',
          tablePhone: String(elm.Mobile).substring(6),
          tableEmail: elm.Email,
          tableCreateTime: elm.CreateTime.replace('T', ' ').substring(0, 19),
          tableCreateIP: elm.CreateIP,
          tableGgAuth: elm.GgAuth ? '已绑定' : '未绑定',
          // tableActivated: elm.Activated ? '已激活' : '未激活'
        })
      })
      this.tableList = tempArray
    }
  }
}
</script>

<style lang="less" scoped>

</style>





